import Title from '@/components/Title'
import { Trim,setStore,getStore,removeStore } from "@/utils/util"
export default {
	data() {
		return {
			order:{},
		}
	},
	computed: {
		
	},
	watch:{
		
	},
	methods: {
		getOrder(){
			const orderId = this.$route.query.orderId;
			this.$http.get("/train/orderDetail",{params:{orderId:orderId}})
				.then((res) => {
					const status = res.data.status;
					if(status==1001){
						this.order = res.data.data;
						return;
					}
					this.$notify.error({
			          title: '请求失败',
			          message: '请检查网络连接或者更换搜索条件',
			          duration: 3000
			        });
				})
		}
	},
	components:{
		Title,
	},
	created() {
		this.getOrder();
	}
}


